*, *::before, *::after {
    box-sizing: border-box;
}

:root {
    --background-color: #262626;
    --outline-color: #fbbf24;
}

html {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}

body {
    background-color: var(--background-color);
    width: 100%;  
    min-height: 100vh;
    /* height: 100%; */
    font-family: "Roboto", sans-serif;  
    display: flex;
    flex-direction: column;
}

.container {
    min-height: calc(100% - 200px);
    width: 100%;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}

.content-header {
    background-color: white;
    color: black;
    text-align: center;
    font-size: 3rem;
    margin-top: 70px;
    display: inline-block;
    border-radius: 10px;
    padding: 10px 20px;

}

.content-item {
    background-color: white;
    color: black;
    font-size: 1.5rem;
    padding: 25px 30px;
    border-radius: 50px;
    font-family: Roboto-light;
    flex: 0 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.content-item.short-description p:last-of-type {
    font-family: Roboto;
    align-self: flex-start;
    margin-top: 50px;
    padding-left: 40px;
}

.content-item .subheader {
    font-family: Roboto;
    font-size: 2rem;
    align-self: flex-start;
    margin-bottom: 25px;
}

ul, li {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

li {
    margin: 20px;
}
#content {
    width: 42%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: center;
    min-height: 200px;
    background-color: rgba(245,254,244, 0.6);   

    display: flex; 
    flex-direction: column;
    font-weight: 100px;
    gap: 50px;

    padding: 70px 150px;
}



header {
    height: 100px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 100px;
    width: 100%;
    align-items: end;
}

footer {
    height: 100px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    gap: 100px;
    width: 100%;
    align-items: center;
    color: white;
    font-size: 2rem;
}
button {
    padding: 10px 20px;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    font-size: 2rem;
    background-color: #404040;
    color: white;
}

.active {
    background-color: #f5f5f4; 
    color: black;

}

/*************** for menue page ***********************/
h1 {
    margin: 0;
    padding: 0;
    font-size: 2.2rem;
    font-family: Roboto;
}

.price-img {
    margin-top: 20px;
    width: 100%;
    justify-content: space-between;
    display: flex;
}
.dishes-title {
    align-self: center;
    padding: 20px 70px;
}

.price-img .pic {
    width: 200px;
    height: 200px;
    border: 2px solid #fbbf24;
}

/******** about page ************/

ul.contact-info {
    width: 80%;
    padding: 0px;
